.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.login {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  color: black;
  z-index: 5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.login-menu {
  border: black 1px solid;
  border-radius: 15px;
  /* width: 50%; */
  padding: 50px;
  margin: 50px;
  box-shadow: 5px 5px 2px 2px #888888;
}
.logo {
  width: 320px;
  margin: 20px 0;
}

.profile-pic {
  width: 100px;
  margin: 20px;
  border-radius: 50%;
}

.login button {
  padding: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 1px #888888;
}

.hide {
  display: none;
}

.menu {
  position: fixed;
  right: 50px;
  bottom: 200px;
}
